ron text-bold"},[_vm._v("Factores ambientales:")]),_vm._v(" son las variables externas al individuo, capaces de influir en su existencia. Los factores ambientales pueden ser abióticos o bióticos.")])])])]),_c('div',{staticClass:"col-md-6 col-lg-4 mb-4"},[_c('figure',[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/temas/tema1/img_8.png"),"alt":"Imagen Descriptiva"}})])])])
